@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
#root{display:flex;flex-direction:column;font-family:Roboto,sans-serif}.header{width:100vw;height:4em;background-color:#fff;box-shadow:0 0 5px #999;z-index:100;position:fixed;top:0;left:0;display:flex;align-items:center;justify-items:flex-start}.header .logo{max-height:80%}.header .website-name{flex:1 1}.header .wallet{justify-self:flex-end;color:#f0f0f0;background-color:#888da5;margin-right:2em;padding:0.2em 1em;border-radius:2em;cursor:pointer}.page{flex:1 1;padding:2em;overflow:hidden;display:flex}main{display:flex;width:100vw;height:100vh;padding-top:4em;padding-left:4em}.charts-page{flex-direction:column}.best-match-page{flex-direction:column;align-items:center}.sidebar{display:flex;flex-direction:column;background-color:#fff;height:calc(100vh - 4em);box-shadow:0 0 5px #999;z-index:99;position:fixed;bottom:0;left:0;padding:1em;width:10em;transition:all 300ms ease;overflow:hidden}.sidebar li{display:flex;margin-bottom:1em;height:2em;width:10em;cursor:pointer}.sidebar svg{font-size:2em;color:#323232}.sidebar img{width:2em}.sidebar a{text-decoration:none;color:#323232;display:flex;align-items:center}.sidebar span{padding-left:1em}.sidebar .horizontal-row{border:1px solid #888da5;width:2em;margin:0 0 1em 0;height:0px;opacity:0.5}.sidebar .sidebar-icon{color:#323232;width:2em;height:2em}.sidebar-closed{width:4em}.animated-button{cursor:pointer;position:relative}.animated-button .toggle-menu{position:absolute;right:0;top:0.5em;width:2em;height:3px;background-color:#323232;transition:all 0.5s ease-in-out}.animated-button .toggle-menu::before{content:"";position:absolute;top:-0.3em;width:2em;height:3px;background-color:#323232;transition:all 0.5s ease-in-out}.animated-button .toggle-menu::after{content:"";position:absolute;top:0.3em;width:1.6em;height:3px;background-color:#323232;transition:all 0.5s ease-in-out}.animated-button .toggle-menu.status-1{-webkit-transform:rotate(360deg);transform:rotate(360deg);background:transparent}.animated-button .toggle-menu.status-1::before{-webkit-transform:rotate(45deg) translate(5px, 8px);transform:rotate(45deg) translate(5px, 8px);top:-0.5em}.animated-button .toggle-menu.status-1::after{width:2em;-webkit-transform:rotate(-45deg) translate(3px, -7px);transform:rotate(-45deg) translate(3px, -7px);top:0.5em}.chain-selection{min-width:14em;margin-right:4em}.table-compare{display:flex;flex-direction:column;max-width:100%}.table-compare .controls{box-shadow:0 0 5px #999;padding:1em;border-radius:0.2em;margin-bottom:2em}.table-compare .table-container{overflow-x:auto;box-shadow:0 0 5px #999}.table-compare .table-container table{border-radius:0.2em}.table-compare .table-container table thead th{border-bottom:1px solid #aaa}.table-compare .table-container table thead th.col-token{min-width:8em;max-width:8em}.table-compare .table-container table thead th.col-rate{min-width:10em;max-width:10em}.table-compare .table-container table tbody tr td{border:1px solid #ccc}.table-compare .cell-token{display:grid;grid-template-columns:1fr;grid-template-areas:"logo" "symbol" "price";align-items:center;justify-content:center}.table-compare .cell-token .logo{grid-area:logo;margin-right:0.2em;width:1.8em;height:1.8em;justify-self:center}.table-compare .cell-token .symbol{grid-area:symbol;font-weight:bold;justify-self:center}.table-compare .cell-token .price{grid-area:price;font-size:0.8em;text-align:center}.table-compare .cell-platform{display:grid;grid-template-areas:"rate rate" "logo name";align-items:center}.table-compare .cell-platform .rate{grid-area:rate;font-size:1.2em;display:flex;align-items:center;justify-self:center}.table-compare .cell-platform .logo{grid-area:logo;justify-self:end;margin-right:0.2em;width:1em;height:1em}.table-compare .cell-platform .name{grid-area:name;font-size:0.8em}.charts{overflow:auto;margin-top:3em}.charts .chart-container{width:100%;height:30vh;margin-bottom:10em}.charts .chart-container h3{text-align:center}.pair-tokens-selection{display:flex;align-items:center;justify-content:space-around}.pair-tokens-selection .token-selection{width:30vw}.pair-tokens-selection .and-separator{font-size:2em;color:#aaa;padding:1em 1em 0 1em}.result{display:flex;align-items:start;justify-content:center;margin-top:5em;padding:1em;box-shadow:0 0 5px #999;border-radius:0.2em}.result table th{padding:0.2em 1em}.result table tr td{padding:0.2em 0.5em;text-align:center}

